namespace $ { export class $mol_row_demo extends $mol_row {

	/// minimal_height 68
	minimal_height() {
		return 68
	}

	/// helloHint @ \Title
	helloHint() {
		return $mol_locale.text( this.locale_contexts() , "helloHint" )
	}

	/// title?val \
	@ $mol_mem()
	title( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// suggest1 @ \Hello
	suggest1() {
		return $mol_locale.text( this.locale_contexts() , "suggest1" )
	}

	/// suggest2 @ \World
	suggest2() {
		return $mol_locale.text( this.locale_contexts() , "suggest2" )
	}

	/// titler $mol_search 
	/// 	hint <= helloHint 
	/// 	query?val <=> title?val 
	/// 	suggests / 
	/// 		<= suggest1 
	/// 		<= suggest2
	@ $mol_mem()
	titler() {
		return new $mol_search().setup( obj => { 
			obj.hint = () => this.helloHint()
			obj.query = ( val? : any ) => this.title( val )
			obj.suggests = () => [].concat( this.suggest1() , this.suggest2() )
		} )
	}

	/// countHint @ \Count
	countHint() {
		return $mol_locale.text( this.locale_contexts() , "countHint" )
	}

	/// count?val null
	@ $mol_mem()
	count( val? : any ) {
		return ( val !== void 0 ) ? val : <any> null
	}

	/// counter $mol_number 
	/// 	hint <= countHint 
	/// 	value?val <=> count?val
	@ $mol_mem()
	counter() {
		return new $mol_number().setup( obj => { 
			obj.hint = () => this.countHint()
			obj.value = ( val? : any ) => this.count( val )
		} )
	}

	/// progress 0.33
	progress() {
		return 0.33
	}

	/// progresser $mol_portion portion <= progress
	@ $mol_mem()
	progresser() {
		return new $mol_portion().setup( obj => { 
			obj.portion = () => this.progress()
		} )
	}

	/// publishLabel @ \Shared
	publishLabel() {
		return $mol_locale.text( this.locale_contexts() , "publishLabel" )
	}

	/// publish?val false
	@ $mol_mem()
	publish( val? : any ) {
		return ( val !== void 0 ) ? val : false
	}

	/// publisher $mol_check_box 
	/// 	label / <= publishLabel 
	/// 	checked?val <=> publish?val
	@ $mol_mem()
	publisher() {
		return new $mol_check_box().setup( obj => { 
			obj.label = () => [].concat( this.publishLabel() )
			obj.checked = ( val? : any ) => this.publish( val )
		} )
	}

	/// dropLabel @ \Drop
	dropLabel() {
		return $mol_locale.text( this.locale_contexts() , "dropLabel" )
	}

	/// buttonDrop $mol_button_minor sub / <= dropLabel
	@ $mol_mem()
	buttonDrop() {
		return new $mol_button_minor().setup( obj => { 
			obj.sub = () => [].concat( this.dropLabel() )
		} )
	}

	/// sub / 
	/// 	<= titler 
	/// 	<= counter 
	/// 	<= progresser 
	/// 	<= publisher 
	/// 	<= buttonDrop
	sub() {
		return [].concat( this.titler() , this.counter() , this.progresser() , this.publisher() , this.buttonDrop() )
	}

} }

