namespace $ { export class $mol_button_demo_major_enabled extends $mol_button_major {

	/// label @ \Click me!
	label() {
		return $mol_locale.text( this.locale_contexts() , "label" )
	}

	/// sub / <= label
	sub() {
		return [].concat( this.label() )
	}

} }

