namespace $ { export class $mol_page_demo extends $mol_page {

	/// title @ \Sign Up
	title() {
		return $mol_locale.text( this.locale_contexts() , "title" )
	}

	/// signup $mol_app_signup
	@ $mol_mem()
	signup() {
		return new $mol_app_signup()
	}

	/// body / <= signup
	body() {
		return [].concat( this.signup() )
	}

	/// rower $mol_row_demo
	@ $mol_mem()
	rower() {
		return new $mol_row_demo()
	}

	/// foot / <= rower
	foot() {
		return [].concat( this.rower() )
	}

} }

