namespace $ { export class $mol_app_habhub extends $mol_page {

	/// title \HabHub
	title() {
		return "HabHub"
	}

	/// gists /
	gists() {
		return [] as any[]
	}

	/// status $mol_status status <= gists
	@ $mol_mem()
	status() {
		return new $mol_status().setup( obj => { 
			obj.status = () => this.gists()
		} )
	}

	/// gist_rows /
	gist_rows() {
		return [] as any[]
	}

	/// list $mol_list rows <= gist_rows
	@ $mol_mem()
	list() {
		return new $mol_list().setup( obj => { 
			obj.rows = () => this.gist_rows()
		} )
	}

	/// body / 
	/// 	<= status 
	/// 	<= list
	body() {
		return [].concat( this.status() , this.list() )
	}

	/// gist_content!id \
	gist_content( id : any ) {
		return ""
	}

	/// Gist_row!id $mol_text text <= gist_content!id
	@ $mol_mem_key()
	Gist_row( id : any ) {
		return new $mol_text().setup( obj => { 
			obj.text = () => this.gist_content(id)
		} )
	}

	/// footer null
	footer() {
		return <any> null
	}

} }

