namespace $ { export class $mol_pop_over_demo extends $mol_pop_over {

	/// align \bottom_right
	align() {
		return "bottom_right"
	}

	/// Anchor \File
	Anchor() {
		return "File"
	}

	/// Open $mol_row sub / \Open
	@ $mol_mem()
	Open() {
		return new $mol_row().setup( obj => { 
			obj.sub = () => [].concat( "Open" )
		} )
	}

	/// Export $mol_row sub / \Export
	@ $mol_mem()
	Export() {
		return new $mol_row().setup( obj => { 
			obj.sub = () => [].concat( "Export" )
		} )
	}

	/// Save $mol_row sub / \Save
	@ $mol_mem()
	Save() {
		return new $mol_row().setup( obj => { 
			obj.sub = () => [].concat( "Save" )
		} )
	}

	/// Menu $mol_list rows / 
	/// 	<= Open 
	/// 	<= Export 
	/// 	<= Save
	@ $mol_mem()
	Menu() {
		return new $mol_list().setup( obj => { 
			obj.rows = () => [].concat( this.Open() , this.Export() , this.Save() )
		} )
	}

	/// bubble_content / <= Menu
	bubble_content() {
		return [].concat( this.Menu() )
	}

} }

