namespace $ { export class $mol_html_head extends $mol_view {

	/// dom_name \head
	dom_name() {
		return "head"
	}

} }

namespace $ { export class $mol_html_body extends $mol_view {

	/// dom_name \body
	dom_name() {
		return "body"
	}

} }

namespace $ { export class $mol_html_title extends $mol_view {

	/// dom_name \title
	dom_name() {
		return "title"
	}

	/// title \
	title() {
		return ""
	}

	/// sub / <= title
	sub() {
		return [].concat( this.title() )
	}

} }

namespace $ { export class $mol_html_meta extends $mol_view {

	/// dom_name \meta
	dom_name() {
		return "meta"
	}

	/// name \
	name() {
		return ""
	}

	/// content \
	content() {
		return ""
	}

	/// charset \utf-8
	charset() {
		return "utf-8"
	}

	/// attr * 
	/// 	name <= name 
	/// 	content <= content 
	/// 	charset <= charset
	attr() {
		return ( { ...super.attr() , 
			"name" :  <any> this.name() ,
			"content" :  <any> this.content() ,
			"charset" :  <any> this.charset() ,
		} )
	}

} }

namespace $ { export class $mol_html_link extends $mol_view {

	/// dom_name \link
	dom_name() {
		return "link"
	}

	/// rel \
	rel() {
		return ""
	}

	/// href \
	href() {
		return ""
	}

	/// attr * 
	/// 	rel <= rel 
	/// 	href <= href
	attr() {
		return ( { ...super.attr() , 
			"rel" :  <any> this.rel() ,
			"href" :  <any> this.href() ,
		} )
	}

} }

