namespace $ { export class $mol_select_demo_strings extends $mol_labeler {

	/// title \Priority
	title() {
		return "Priority"
	}

	/// priority?val \Lowest
	@ $mol_mem()
	priority( val? : any ) {
		return ( val !== void 0 ) ? val : "Lowest"
	}

	/// Content $mol_select 
	/// 	value?val <=> priority?val 
	/// 	options / 
	/// 		\Highest 
	/// 		\High
	/// 		\Medium
	/// 		\Low
	/// 		\Lowest
	/// 	clearable false
	@ $mol_mem()
	Content() {
		return new $mol_select().setup( obj => { 
			obj.value = ( val? : any ) => this.priority( val )
			obj.options = () => [].concat( "Highest " , "High" , "Medium" , "Low" , "Lowest" )
			obj.clearable = () => false
		} )
	}

} }

