namespace $ { export class $mol_text_demo extends $mol_scroll {

	/// texter $mol_text text \
	/// 	\# [Benchmarks](perf)
	/// 	\## Benchmark 1
	/// 	\### Benchmark 1.1
	/// 	\#### Benchmark 1.1.1
	/// 	\##### Benchmark 1.1.1.1
	/// 	\
	/// 	\* $mol_perf_render - simple benchmark of rendering ([online](http://eigenmethod.github.io/mol/perf/render/))
	/// 	\* [ToDoMVC benchmark](https://github.com/nin-jin/todomvc/tree/master/benchmark)
	/// 	\
	/// 	\# Quick start
	/// 	\
	/// 	\**Create MAM project**
	/// 	\
	/// 	\Easy way is checkout [this preconfigured ~~PMS~~MAM repository](http://github.com/eigenmethod/mam/) and start dev server:
	/// 	\
	/// 	\```sh
	/// 	\git clone https://github.com/eigenmethod/mam.git ./mam && cd mam
	/// 	\npm start
	/// 	\```
	/// 	\
	/// 	\|           | Column 1 | Column 2 | Column 3
	/// 	\|-----------|----------|----------|---------
	/// 	\| **Row 1** | Cell 1x1 | Cell 2x1 | Cell 3x1
	/// 	\| **Row 2** | Cell 1x2 | Cell 2x2 | Cell 3x2
	/// 	\| **Row 3** | Cell 1x3 | Cell 2x3 | Cell 3x3
	/// 	\| **Row 4** | Cell 1x4 | Cell 2x4 | Cell 3x4
	/// 	\| **Row 5** | Cell 1x5 | Cell 2x5 | Cell 3x5
	/// 	\| **Row 6** | Cell 1x6 | Cell 2x6 | Cell 3x6
	/// 	\| **Row 7** | Cell 1x7 | Cell 2x7 | Cell 3x7
	/// 	\| **Row 8** | Cell 1x8 | Cell 2x8 | Cell 3x8
	/// 	\| **Row 9** | Cell 1x9 | Cell 2x9 | Cell 3x9
	/// 	\
	/// 	\Build status: [![Build Status](https://travis-ci.org/eigenmethod/mol.svg?branch=master)](https://travis-ci.org/eigenmethod/mol)
	/// 	\
	@ $mol_mem()
	texter() {
		return new $mol_text().setup( obj => { 
			obj.text = () => "# [Benchmarks](perf)\n## Benchmark 1\n### Benchmark 1.1\n#### Benchmark 1.1.1\n##### Benchmark 1.1.1.1\n\n* $mol_perf_render - simple benchmark of rendering ([online](http://eigenmethod.github.io/mol/perf/render/))\n* [ToDoMVC benchmark](https://github.com/nin-jin/todomvc/tree/master/benchmark)\n\n# Quick start\n\n**Create MAM project**\n\nEasy way is checkout [this preconfigured ~~PMS~~MAM repository](http://github.com/eigenmethod/mam/) and start dev server:\n\n```sh\ngit clone https://github.com/eigenmethod/mam.git ./mam && cd mam\nnpm start\n```\n\n|           | Column 1 | Column 2 | Column 3\n|-----------|----------|----------|---------\n| **Row 1** | Cell 1x1 | Cell 2x1 | Cell 3x1\n| **Row 2** | Cell 1x2 | Cell 2x2 | Cell 3x2\n| **Row 3** | Cell 1x3 | Cell 2x3 | Cell 3x3\n| **Row 4** | Cell 1x4 | Cell 2x4 | Cell 3x4\n| **Row 5** | Cell 1x5 | Cell 2x5 | Cell 3x5\n| **Row 6** | Cell 1x6 | Cell 2x6 | Cell 3x6\n| **Row 7** | Cell 1x7 | Cell 2x7 | Cell 3x7\n| **Row 8** | Cell 1x8 | Cell 2x8 | Cell 3x8\n| **Row 9** | Cell 1x9 | Cell 2x9 | Cell 3x9\n\nBuild status: [![Build Status](https://travis-ci.org/eigenmethod/mol.svg?branch=master)](https://travis-ci.org/eigenmethod/mol)\n"
		} )
	}

	/// sub / <= texter
	sub() {
		return [].concat( this.texter() )
	}

} }

