namespace $ { export class $mol_app_quine extends $mol_page {

	/// content \
	content() {
		return ""
	}

	/// texter $mol_text text <= content
	@ $mol_mem()
	texter() {
		return new $mol_text().setup( obj => { 
			obj.text = () => this.content()
		} )
	}

	/// body / <= texter
	body() {
		return [].concat( this.texter() )
	}

} }

