namespace $ { export class $mol_demo extends $mol_view {

	/// name \$mol_view
	name() {
		return "$mol_view"
	}

	/// title <= name
	title() {
		return this.name()
	}

	/// titler $mol_link 
	/// 	arg * demo <= name 
	/// 	sub / <= title
	@ $mol_mem()
	titler() {
		return new $mol_link().setup( obj => { 
			obj.arg = () => ({
			"demo" :  <any> this.name() ,
		})
			obj.sub = () => [].concat( this.title() )
		} )
	}

	/// header $mol_view sub / <= titler
	@ $mol_mem()
	header() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( this.titler() )
		} )
	}

	/// widget null
	widget() {
		return <any> null
	}

	/// screener $mol_view sub / <= widget
	@ $mol_mem()
	screener() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( this.widget() )
		} )
	}

	/// sub / 
	/// 	<= header 
	/// 	<= screener
	sub() {
		return [].concat( this.header() , this.screener() )
	}

} }

