namespace $ { export class $mol_pop_demo extends $mol_row {

	/// showed_value?val false
	@ $mol_mem()
	showed_value( val? : any ) {
		return ( val !== void 0 ) ? val : false
	}

	/// event_show?event null
	@ $mol_mem()
	event_show( event? : any ) {
		return ( event !== void 0 ) ? event : <any> null
	}

	/// Show $mol_button 
	/// 	event_click?event <=> event_show?event 
	/// 	sub / \Show Bubble
	@ $mol_mem()
	Show() {
		return new $mol_button().setup( obj => { 
			obj.event_click = ( event? : any ) => this.event_show( event )
			obj.sub = () => [].concat( "Show Bubble" )
		} )
	}

	/// event_hide?event null
	@ $mol_mem()
	event_hide( event? : any ) {
		return ( event !== void 0 ) ? event : <any> null
	}

	/// Hide $mol_button 
	/// 	event_click?event <=> event_hide?event 
	/// 	sub / \Hide Bubble
	@ $mol_mem()
	Hide() {
		return new $mol_button().setup( obj => { 
			obj.event_click = ( event? : any ) => this.event_hide( event )
			obj.sub = () => [].concat( "Hide Bubble" )
		} )
	}

	/// Content $mol_view sub / 
	/// 	\ I'm bubble, i showed when you want 
	/// 	<= Hide
	@ $mol_mem()
	Content() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( " I'm bubble, i showed when you want " , this.Hide() )
		} )
	}

	/// Pop $mol_pop 
	/// 	showed?val <= showed_value?val 
	/// 	align <= align_value?val 
	/// 	Anchor <= Show 
	/// 	bubble_content / <= Content
	@ $mol_mem()
	Pop() {
		return new $mol_pop().setup( obj => { 
			obj.showed = ( val? : any ) => this.showed_value()
			obj.align = () => this.align_value()
			obj.Anchor = () => this.Show()
			obj.bubble_content = () => [].concat( this.Content() )
		} )
	}

	/// align_value?val \bottom_center
	@ $mol_mem()
	align_value( val? : any ) {
		return ( val !== void 0 ) ? val : "bottom_center"
	}

	/// Select_label $mol_labeler 
	/// 	title \align
	/// 	Content $mol_select 
	/// 		clearable false 
	/// 		value?val <=> align_value?val 
	/// 		options / 
	/// 			\left_top
	/// 			\left_center
	/// 			\left_bottom
	/// 			\right_top
	/// 			\right_center
	/// 			\right_bottom
	/// 			\center
	/// 			\top_left
	/// 			\top_center
	/// 			\top_right
	/// 			\bottom_left
	/// 			\bottom_center
	/// 			\bottom_right
	@ $mol_mem()
	Select_label() {
		return new $mol_labeler().setup( obj => { 
			obj.title = () => "align"
			obj.Content = () => new $mol_select().setup( obj => { 
			obj.clearable = () => false
			obj.value = ( val? : any ) => this.align_value( val )
			obj.options = () => [].concat( "left_top" , "left_center" , "left_bottom" , "right_top" , "right_center" , "right_bottom" , "center" , "top_left" , "top_center" , "top_right" , "bottom_left" , "bottom_center" , "bottom_right" )
		} )
		} )
	}

	/// sub / 
	/// 	<= Pop 
	/// 	<= Select_label
	sub() {
		return [].concat( this.Pop() , this.Select_label() )
	}

} }

