namespace $ { export class $mol_section_demo extends $mol_scroll {

	/// Section $mol_section 
	/// 	head \Section header
	/// 	Content $mol_filler
	@ $mol_mem()
	Section() {
		return new $mol_section().setup( obj => { 
			obj.head = () => "Section header"
			obj.Content = () => new $mol_filler()
		} )
	}

	/// sub / <= Section
	sub() {
		return [].concat( this.Section() )
	}

} }

