namespace $ { export class $mol_bar_demo_search extends $mol_bar {

	/// value?val \
	@ $mol_mem()
	value( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// stringer $mol_string value?val <=> value?val
	@ $mol_mem()
	stringer() {
		return new $mol_string().setup( obj => { 
			obj.value = ( val? : any ) => this.value( val )
		} )
	}

	/// submitter $mol_button sub / \submit
	@ $mol_mem()
	submitter() {
		return new $mol_button().setup( obj => { 
			obj.sub = () => [].concat( "submit" )
		} )
	}

	/// sub / 
	/// 	<= stringer 
	/// 	<= submitter
	sub() {
		return [].concat( this.stringer() , this.submitter() )
	}

} }

