namespace $ { export class $mol_select_demo_search extends $mol_labeler {

	/// title \Month
	title() {
		return "Month"
	}

	/// month?val \
	@ $mol_mem()
	month( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// months * 
	/// 	jan \January
	/// 	feb \February
	/// 	mar \March
	/// 	apr \April
	/// 	may \May
	/// 	jun \June
	/// 	jul \July
	/// 	aug \August
	/// 	sep \September
	/// 	oct \October
	/// 	nov \November
	/// 	dec \December
	months() {
		return ({
			"jan" :  <any> "January" ,
			"feb" :  <any> "February" ,
			"mar" :  <any> "March" ,
			"apr" :  <any> "April" ,
			"may" :  <any> "May" ,
			"jun" :  <any> "June" ,
			"jul" :  <any> "July" ,
			"aug" :  <any> "August" ,
			"sep" :  <any> "September" ,
			"oct" :  <any> "October" ,
			"nov" :  <any> "November" ,
			"dec" :  <any> "December" ,
		})
	}

	/// Content $mol_select 
	/// 	no_options_message \Not found
	/// 	value?val <=> month?val 
	/// 	dictionary <= months
	@ $mol_mem()
	Content() {
		return new $mol_select().setup( obj => { 
			obj.no_options_message = () => "Not found"
			obj.value = ( val? : any ) => this.month( val )
			obj.dictionary = () => this.months()
		} )
	}

} }

