namespace $ { export class $mol_link_demo extends $mol_row {

	/// labelRed @ \Red
	labelRed() {
		return $mol_locale.text( this.locale_contexts() , "labelRed" )
	}

	/// linkRed $mol_link 
	/// 	arg * color \red
	/// 	sub / <= labelRed
	@ $mol_mem()
	linkRed() {
		return new $mol_link().setup( obj => { 
			obj.arg = () => ({
			"color" :  <any> "red" ,
		})
			obj.sub = () => [].concat( this.labelRed() )
		} )
	}

	/// labelGreen @ \Green
	labelGreen() {
		return $mol_locale.text( this.locale_contexts() , "labelGreen" )
	}

	/// linkGreen $mol_link 
	/// 	arg * color \green
	/// 	sub / <= labelGreen
	@ $mol_mem()
	linkGreen() {
		return new $mol_link().setup( obj => { 
			obj.arg = () => ({
			"color" :  <any> "green" ,
		})
			obj.sub = () => [].concat( this.labelGreen() )
		} )
	}

	/// labelBlue @ \Blue
	labelBlue() {
		return $mol_locale.text( this.locale_contexts() , "labelBlue" )
	}

	/// linkBlue $mol_link 
	/// 	arg * color \blue
	/// 	sub / <= labelBlue
	@ $mol_mem()
	linkBlue() {
		return new $mol_link().setup( obj => { 
			obj.arg = () => ({
			"color" :  <any> "blue" ,
		})
			obj.sub = () => [].concat( this.labelBlue() )
		} )
	}

	/// linkExternal $mol_link 
	/// 	uri \http://example.org
	/// 	sub / \example.org
	@ $mol_mem()
	linkExternal() {
		return new $mol_link().setup( obj => { 
			obj.uri = () => "http://example.org"
			obj.sub = () => [].concat( "example.org" )
		} )
	}

	/// sub / 
	/// 	<= linkRed 
	/// 	<= linkGreen 
	/// 	<= linkBlue 
	/// 	<= linkExternal
	sub() {
		return [].concat( this.linkRed() , this.linkGreen() , this.linkBlue() , this.linkExternal() )
	}

} }

