namespace $ { export class $mol_meter_demo extends $mol_card {

	top(){
		return this.Meter().top()
	}

	height(){
		return this.Meter().height()
	}

	/// Meter $mol_meter 
	/// 	top => top 
	/// 	height => height
	@ $mol_mem()
	Meter() {
		return new $mol_meter()
	}

	/// plugins / <= Meter
	plugins() {
		return [].concat( this.Meter() )
	}

	/// Top $mol_view sub / 
	/// 	\Offset from top is 
	/// 	<= top 
	/// 	\px
	@ $mol_mem()
	Top() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( "Offset from top is " , this.top() , "px" )
		} )
	}

	/// Height $mol_view sub / 
	/// 	\Component height is 
	/// 	<= height 
	/// 	\px
	@ $mol_mem()
	Height() {
		return new $mol_view().setup( obj => { 
			obj.sub = () => [].concat( "Component height is " , this.height() , "px" )
		} )
	}

	/// sub / 
	/// 	<= Top 
	/// 	<= Height
	sub() {
		return [].concat( this.Top() , this.Height() )
	}

} }

