namespace $ { export class $mol_app_inventory_controller_demo extends $mol_app_inventory_controller {

	/// title1 @ \Notebook
	title1() {
		return $mol_locale.text( this.locale_contexts() , "title1" )
	}

	/// description1 @ \Fast, compact and lightweight
	description1() {
		return $mol_locale.text( this.locale_contexts() , "description1" )
	}

	/// count1?val 1
	@ $mol_mem()
	count1( val? : any ) {
		return ( val !== void 0 ) ? val : 1
	}

	/// status1?val \pending
	@ $mol_mem()
	status1( val? : any ) {
		return ( val !== void 0 ) ? val : "pending"
	}

	/// Position1 $mol_app_inventory_position 
	/// 	title <= title1 
	/// 	description <= description1 
	/// 	count?val <=> count1?val 
	/// 	status?val <=> status1?val
	@ $mol_mem()
	Position1() {
		return new $mol_app_inventory_position().setup( obj => { 
			obj.title = () => this.title1()
			obj.description = () => this.description1()
			obj.count = ( val? : any ) => this.count1( val )
			obj.status = ( val? : any ) => this.status1( val )
		} )
	}

	/// title2 @ \Mouse
	title2() {
		return $mol_locale.text( this.locale_contexts() , "title2" )
	}

	/// description2 @ \Laser 1000dpi!!!
	description2() {
		return $mol_locale.text( this.locale_contexts() , "description2" )
	}

	/// count2?val 10
	@ $mol_mem()
	count2( val? : any ) {
		return ( val !== void 0 ) ? val : 10
	}

	/// status2?val \approved
	@ $mol_mem()
	status2( val? : any ) {
		return ( val !== void 0 ) ? val : "approved"
	}

	/// Position2 $mol_app_inventory_position 
	/// 	title <= title2 
	/// 	description <= description2 
	/// 	count?val <=> count2?val 
	/// 	status?val <=> status2?val
	@ $mol_mem()
	Position2() {
		return new $mol_app_inventory_position().setup( obj => { 
			obj.title = () => this.title2()
			obj.description = () => this.description2()
			obj.count = ( val? : any ) => this.count2( val )
			obj.status = ( val? : any ) => this.status2( val )
		} )
	}

	/// position_rows / 
	/// 	<= Position1 
	/// 	<= Position2
	position_rows() {
		return [].concat( this.Position1() , this.Position2() )
	}

} }

