namespace $ { export class $mol_float_demo extends $mol_scroll {

	/// floaterContent $mol_card_demo_hello
	@ $mol_mem()
	floaterContent() {
		return new $mol_card_demo_hello()
	}

	/// floater $mol_float sub / <= floaterContent
	@ $mol_mem()
	floater() {
		return new $mol_float().setup( obj => { 
			obj.sub = () => [].concat( this.floaterContent() )
		} )
	}

	/// filler1 $mol_filler
	@ $mol_mem()
	filler1() {
		return new $mol_filler()
	}

	/// filler2 $mol_filler
	@ $mol_mem()
	filler2() {
		return new $mol_filler()
	}

	/// contenter $mol_row sub / 
	/// 	<= filler1 
	/// 	<= filler2
	@ $mol_mem()
	contenter() {
		return new $mol_row().setup( obj => { 
			obj.sub = () => [].concat( this.filler1() , this.filler2() )
		} )
	}

	/// content / <= contenter
	content() {
		return [].concat( this.contenter() )
	}

	/// sub / 
	/// 	<= floater 
	/// 	<= content
	sub() {
		return [].concat( this.floater() , this.content() )
	}

} }

