namespace $ { export class $mol_app_supplies_position_demo extends $mol_app_supplies_position {

	/// product_name @ \Bread
	product_name() {
		return $mol_locale.text( this.locale_contexts() , "product_name" )
	}

	/// price $mol_unit_money_usd valueOf 1
	@ $mol_mem()
	price() {
		return new $mol_unit_money_usd().setup( obj => { 
			obj.valueOf = () => 1
		} )
	}

	/// quantity \100
	quantity() {
		return "100"
	}

	/// cost $mol_unit_money_usd valueOf 100
	@ $mol_mem()
	cost() {
		return new $mol_unit_money_usd().setup( obj => { 
			obj.valueOf = () => 100
		} )
	}

	/// supply_date \2016-01-13
	supply_date() {
		return "2016-01-13"
	}

	/// division_name @ \Food
	division_name() {
		return $mol_locale.text( this.locale_contexts() , "division_name" )
	}

	/// store_name @ \Main office
	store_name() {
		return $mol_locale.text( this.locale_contexts() , "store_name" )
	}

} }

