namespace $ { export class $mol_switch_demo_enabled extends $mol_switch {

	/// color?val \red
	@ $mol_mem()
	color( val? : any ) {
		return ( val !== void 0 ) ? val : "red"
	}

	/// value?val <=> color?val
	@ $mol_mem()
	value( val? : any ) {
		return this.color( val )
	}

	/// option_red @ \Red
	option_red() {
		return $mol_locale.text( this.locale_contexts() , "option_red" )
	}

	/// option_green @ \Green
	option_green() {
		return $mol_locale.text( this.locale_contexts() , "option_green" )
	}

	/// option_blue @ \Blue
	option_blue() {
		return $mol_locale.text( this.locale_contexts() , "option_blue" )
	}

	/// options * 
	/// 	red <= option_red 
	/// 	green <= option_green 
	/// 	blue <= option_blue
	options() {
		return ( { ...super.options() , 
			"red" :  <any> this.option_red() ,
			"green" :  <any> this.option_green() ,
			"blue" :  <any> this.option_blue() ,
		} )
	}

} }

