namespace $ { export class $mol_list_demo extends $mol_scroll {

	/// rows /
	rows() {
		return [] as any[]
	}

	/// lister $mol_list rows <= rows
	@ $mol_mem()
	lister() {
		return new $mol_list().setup( obj => { 
			obj.rows = () => this.rows()
		} )
	}

	/// sub / <= lister
	sub() {
		return [].concat( this.lister() )
	}

} }

