namespace $ { export class $mol_labeler_demo_text extends $mol_labeler {

	/// title @ \Provider
	title() {
		return $mol_locale.text( this.locale_contexts() , "title" )
	}

	/// content @ \ACME Provider Inc.
	content() {
		return $mol_locale.text( this.locale_contexts() , "content" )
	}

} }

namespace $ { export class $mol_labeler_demo_stringer extends $mol_labeler {

	/// title @ \User name
	title() {
		return $mol_locale.text( this.locale_contexts() , "title" )
	}

	/// hint @ \Jack Sparrow
	hint() {
		return $mol_locale.text( this.locale_contexts() , "hint" )
	}

	/// user_name?val \
	@ $mol_mem()
	user_name( val? : any ) {
		return ( val !== void 0 ) ? val : ""
	}

	/// Content $mol_string 
	/// 	hint <= hint 
	/// 	value?val <=> user_name?val
	@ $mol_mem()
	Content() {
		return new $mol_string().setup( obj => { 
			obj.hint = () => this.hint()
			obj.value = ( val? : any ) => this.user_name( val )
		} )
	}

} }

